import React from 'react';
import { makeStyles } from '@material-ui/core/styles';
import Chip from '@material-ui/core/Chip';
import { useFoodStateValue } from '../context/FoodStateContext';

const useStyles = makeStyles((theme) => ({
  root: {
    // display: 'flex',
    '& > *': {
      margin: theme.spacing(0.5),
    },
    whiteSpace: 'nowrap',
    height: '40px',
    overflowY: 'scroll',
  },
}));

export const Chips = () => {
  const classes = useStyles();
  const { foodState } = useFoodStateValue();

  const handleClick = () => {
    console.info('You?');
  };

  return (
    <div className={classes.chipsContainer}>
      <div className={classes.root}>
        {foodState
          .filter((item) => item.category === 'seasoning')
          .map((food) => {
            return (
              <Chip
                className={`chip chip--${food.grammageInfo.type} `}
                label={food.title}
                onClick={handleClick}
                clickable
                variant={'outlined'}
                color='primary'
              />
              //! color scheme light for chips when greyed out => maybe 3 colors for differect category?
              //! add an icon when greyed out
            );
          })}
      </div>
    </div>
  );
};
